<template>
    <div class="step2">
        <el-col>
            <el-select v-model="value" placeholder="Escolha">
                <el-option v-for="item in lanches" :label="item.label" :value="item.value"></el-option>
            </el-select>
        </el-col>
        <el-col>
            <el-input-number v-model="qtde":min="1" :max="10"></el-input-number>
        </el-col>
    </div>
</template>
<script>

    

    export default {
        data() {
            return {
                lanches: [{
                        label: 'teste1',
                        value: 'value1'
                    },
                    {
                        label: 'teste2',
                        value: 'value2'
                    },
                    {
                        label: 'teste3', 
                        value: 'value3'
                }],
                value: '',
                qtde: 1
            }
        },
        methods: {

        }
    }
</script>
<style scoped>

    .step2 {
        display: flex;
        flex-direction: column;
    }
    .step2 > div {
        margin: 15px 0;
    }
</style>


